html {
	box-sizing: border-box;
}

*,
::before,
::after {
	box-sizing: inherit;
}

a {
	color: inherit;
	text-decoration: none;
}

img {
	max-width: 100%;
}

h1,
h2,
h3,
h4,
p {
	margin: 0;
}

:root {
	--black: #111;
	--dark: #3f4e65;
	--white: #fff;
	--blue-light-gradient: linear-gradient(180deg, #6cd0e9 14.1%, #6cade9 57.69%);
	--blue-dark-gradient: linear-gradient(90deg, #39acfd 0%, #5eb9ff 97.25%);
	--yellow: #ffc85e;
	--orange: #f3a50d;
	--grey: #EDEDED;
	--text-grey: #5C6370;
	--green: #1FD85D;
	--yellow-gradient: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
	--shadow: 0px 14px 39px -11px rgba(0, 0, 0, 0.06);
}

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

body {
	margin: 0 auto;
	font-family: 'Inter', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: var(--white);
}